import React, { Component }   from 'react';
import './App.css';


class App extends React.Component {

constructor(){
  super()

  this.setState ={
    showChild : true,
    text: ' '
  };

}

render() {
 return(
  <div>
  <header className="App-header"> 

  <button onClick= {() =>
  this.setState(state => ({
    showChild: !state.showChild
  }))
 }> Toggle Lifecycles
  </button>
  <button onClick={() =>
    this.setState(state =>
  ({ text: state.text+ "hello"})
  ) }>  Update Text
  </button>
  { this.state.showChild ?
  <lifecycly text = {this.state.text}/>
  : null }
  
  </header>
  </div>

    );

 
  }
}

export default App;
